body {
  margin: 0px 120px; /* Default margin */
}

@media screen and  (max-width: 1200px) {
  body {
    margin: 0px 80px; /* Default margin */
  }

}
@media screen and  (max-width: 900px) {
  body {
    margin: 0px 20px; /* Default margin */
  }
  
  .row{
    flex-direction: column;
    gap: 64px !important;
  }
  .processContainer{
    flex-direction: column;
  }
  .processContainer > img{
width: 80vw;
  }
  .heroSection > :nth-child(2){
    padding-top: 16px;
  width: 100vw !important;
  font-size: 20px;
  }
  .heroSection{
    height: 65vh !important;
  }
  .articleContainer{
    flex-direction: column !important;
    gap: 16px !important;
  }

}




h1{
  font-size: 80px;
 padding: 0;
 margin: 0;
}
h2{
  font-size: 40px;
  padding: 0;
  margin: 0;
}
h3{
  font-size: 20px;
  padding: 0;
  margin: 0;
}
p{
  font-size: 14px;
  padding: 0;
  margin: 0;
}


.NavBar{
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid black;
  width: 100%;
  padding: 20px 0px;
}
a{
  font-size: 20px;
  text-decoration: none;
  color: black;
  font-weight: 700;
}
.sections{
  margin-top: 148px;
}

.heroSection{
  height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;

}
.heroSection > :nth-child(2){
  padding-top: 16px;
width: 30vw;
font-size: 20px;
}



.column{
display: flex;
flex-direction: column;
align-items: flex-start;
justify-content: flex-start;
gap:40px;

}
.row{
display: flex;
width: 100%;
justify-content: space-between;
align-content: space-between;
align-items: flex-start;
gap: 48px;
}

.processContainer{
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-content: space-between;
  align-items: flex-start;
  gap: 40px;
  margin-bottom: 88px;
}
.processContainer > :nth-child(1){
  flex: 1;
}
.processContainer > :nth-child(2){
  flex: 2;
}

.serviceContainer{
  display: flex;
  flex-direction: column;
  align-content: flex-start;
  align-items: flex-start;
  border-top: 1px solid black;
  flex: 1;
  padding: 16px 16px 0px 16px;
  gap: 8px;
}
.articleContainer{
  display: flex;
  width: 100%;
  flex-direction: row;
  align-content: flex-start;
  align-items: flex-start;
  justify-content: space-between;
  border-top: 1px solid black;
  padding: 16px 16px 0px 16px;
  gap: 120px;
}

.articleContainer > :first-child{
  flex:1;
}
.articleContainer > :nth-child(2){
  flex:1;
}


.projectContainer{
  display: flex;
  width: 100%;
  flex-direction: column;
  align-content: flex-start;
  align-items: flex-start;
  justify-content: space-between;
  gap: 8px;
  flex:1;
}

.projectImg{
  background-color: #FBFBFB;
  background-image: url('./Img/point.svg');
  background-repeat: repeat;
  width: 100%;
  border-radius: 8px;
  border: 1px solid black;
  height: 35Vh;
 
}
.updateImg{
  background-color: #FBFBFB;
  background-image: url('./Img/point.svg');
  background-repeat: repeat;
  width: 100%;
  border-radius: 8px;
  border: 1px solid black;
  height: 25Vh;
 
}

.updatesContainer{
  display: flex;
  width: 100%;
  flex-direction: column;
  align-content: flex-start;
  align-items: flex-start;
  justify-content: space-between;
  gap: 8px;
  flex:1;
}

.projectContainer:hover{ 
  
transition: color 0.4s ease;
color:#4F64D7;
}
.projectContainer:not(:hover) {
  color: #000000;
  transition: color 0.4s ease;
}

.projectContainer:hover .projectImg { 
  border-color:#4F64D7 ;
  transition: height 0.4s ease;
}
.projectContainer:not(:hover) .projectImg{ 
  border-color:#000000 ;
  transition: height 0.4s ease;
}



.footer{
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid black;
  width: 100%;
  padding: 20px 0px;
  margin-top: 56px;
}



.scroll-animation {
height: 2500px;
}

.fixed-container {
  position:fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.outer-path{
  fill: none;
  stroke: #000000;
  stroke-width: 1px;
  stroke-dasharray: 600;
  stroke-dashoffset: 20;
  animation: draw 2s linear forwards;
}






.outer-circle {
  fill: none;
  stroke: #000000;
  stroke-width: 1px;  
  stroke-dasharray: 900;
  stroke-dashoffset:900;
  transition: stroke-dashoffset 1s ease, fill 1s ease;
}

.outer-circle.active {
  stroke-dashoffset: 0;
  fill: #F2F2F2;
  
}



.outer-type {
  fill: none;
  transition:  fill 1s ease;
}

.outer-type.active {
  stroke-dashoffset: 0;
  fill: black;
  
}
.s.active:hover {
  fill: greenyellow; /* Change the stroke color on hover */
  r: 90;
}
.s.active {
  stroke-dashoffset: 0;
  fill: #F7D750;
  
}


.body{
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.body h1{
  font-size: 120px;
  flex:1;
}
.body .row img {
height: 72px;
}